.create-post-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background: #f8f9fa;
  min-height: 100vh;
}

.create-header {
  text-align: center;
  margin-bottom: 30px;
  padding: 30px 20px;
  background: white;
  border-radius: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);

  h1 {
    font-size: 2.2em;
    font-weight: 700;
    margin: 0 0 8px 0;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  .create-subtitle {
    color: #6c757d;
    font-size: 1.1em;
    margin: 0;
    font-weight: 400;
  }
}

.create-form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  align-items: start;
}

.form-content {
  background: white;
  border-radius: 15px;
  padding: 30px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);

  .form-group {
    margin-bottom: 25px;

    label {
      display: block;
      margin-bottom: 8px;
      font-weight: 600;
      color: #333;
      font-size: 0.95em;
    }

    .form-input, .form-textarea {
      width: 100%;
      padding: 12px 16px;
      border: 2px solid #e9ecef;
      border-radius: 10px;
      font-size: 14px;
      box-sizing: border-box;
      transition: all 0.3s ease;
      font-family: inherit;

      &:focus {
        outline: none;
        border-color: #667eea;
        box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
        transform: translateY(-1px);
      }

      &::placeholder {
        color: #adb5bd;
      }
    }

    .form-textarea {
      resize: vertical;
      min-height: 100px;
      line-height: 1.5;
    }

    .location-input-container {
      position: relative;
      display: flex;
      align-items: center;

      .location-icon {
        position: absolute;
        left: 12px;
        z-index: 1;
        font-size: 16px;
      }

      .location-input {
        padding-left: 40px;
      }
    }

    .image-upload-zone {
      border: 2px dashed #dee2e6;
      border-radius: 12px;
      padding: 30px;
      text-align: center;
      cursor: pointer;
      transition: all 0.3s ease;
      background: #f8f9fa;
      position: relative;
      overflow: hidden;

      &:hover {
        border-color: #667eea;
        background: #f0f3ff;
        transform: translateY(-2px);
      }

      &.has-image {
        padding: 0;
        background: transparent;
        border: none;

        &:hover {
          transform: none;
        }
      }

      .file-input {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        cursor: pointer;
        z-index: 2;
      }

      .upload-content {
        pointer-events: none;

        .upload-icon {
          font-size: 3em;
          margin-bottom: 15px;
          opacity: 0.7;
        }

        .upload-text {
          font-weight: 600;
          color: #495057;
          margin: 0 0 5px 0;
          font-size: 1.1em;
        }

        .upload-hint {
          color: #6c757d;
          font-size: 0.85em;
          margin: 0;
        }
      }

      .image-preview-container {
        position: relative;
        border-radius: 12px;
        overflow: hidden;

        .upload-preview {
          width: 100%;
          height: 200px;
          object-fit: cover;
          display: block;
        }

        .image-overlay {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: rgba(0, 0, 0, 0.7);
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 15px;
          opacity: 0;
          transition: opacity 0.3s ease;

          .remove-btn, .change-btn {
            padding: 8px 16px;
            border-radius: 20px;
            font-size: 0.85em;
            font-weight: 600;
            cursor: pointer;
            border: none;
            display: flex;
            align-items: center;
            gap: 5px;
            transition: transform 0.2s ease;

            &:hover {
              transform: translateY(-2px);
            }
          }

          .remove-btn {
            background: #dc3545;
            color: white;
          }

          .change-btn {
            background: #007bff;
            color: white;
          }
        }

        &:hover .image-overlay {
          opacity: 1;
        }
      }
    }

    .error-message {
      margin-top: 8px;
      color: #dc3545;
      font-size: 0.85em;
      display: flex;
      align-items: center;
      gap: 5px;
      font-weight: 500;

      .error-icon {
        font-size: 14px;
      }
    }
  }
}

// Styles pour la pr√©visualisation
.post-preview {
  background: white;
  border-radius: 15px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  overflow: hidden;
  margin-bottom: 20px;

  .preview-header {
    padding: 20px;
    border-bottom: 1px solid #f0f0f0;

    .user-info {
      display: flex;
      align-items: center;
      gap: 12px;

      .user-avatar {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 700;
        font-size: 18px;
        color: white;
        text-transform: uppercase;
        box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
        border: 3px solid white;
        position: relative;
        
        &::before {
          content: '';
          position: absolute;
          top: -3px;
          left: -3px;
          right: -3px;
          bottom: -3px;
          border-radius: 50%;
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          z-index: -1;
          opacity: 0.2;
          animation: pulse 2s infinite;
        }
      }

      .user-details {
        flex: 1;

        .preview-title {
          margin: 0;
          font-size: 1.2em;
          font-weight: 600;
          color: #333;
          line-height: 1.2;
        }
      }
    }
  }

  .preview-content {
    padding: 20px;

    .preview-description {
      margin: 0 0 15px 0;
      color: #555;
      line-height: 1.5;
      font-size: 0.95em;
    }

    .preview-image-container {
      margin: 15px 0;
      border-radius: 12px;
      overflow: hidden;
      background: #f8f9fa;

      .preview-image {
        width: 100%;
        height: 200px;
        object-fit: cover;
        display: block;
      }

      .preview-placeholder {
        height: 200px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: #999;
        background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);

        .placeholder-icon {
          font-size: 3em;
          margin-bottom: 10px;
          opacity: 0.5;
        }

        .placeholder-text {
          font-size: 0.9em;
          font-weight: 500;
        }
      }
    }

    .preview-location {
      color: #666;
      font-size: 0.9em;
      margin-top: 10px;
      font-weight: 500;
    }
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 0.2;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.1;
  }
  100% {
    transform: scale(1);
    opacity: 0.2;
  }
}

.form-actions {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-top: 30px;

  .form-actions {
    display: flex;
    gap: 15px;
    justify-content: center;
    margin-top: 30px;
    padding-top: 20px;
    border-top: 1px solid #f0f0f0;

    .submit-btn, .cancel-btn {
      padding: 14px 28px;
      border: none;
      border-radius: 25px;
      font-size: 14px;
      font-weight: 600;
      cursor: pointer;
      transition: all 0.3s ease;
      display: flex;
      align-items: center;
      gap: 8px;
      min-width: 140px;
      justify-content: center;

      .btn-icon {
        font-size: 16px;
      }
    }

    .submit-btn {
      background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
      color: white;
      box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);

      &:hover:not(:disabled) {
        transform: translateY(-2px);
        box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
      }

      &:disabled {
        background: #6c757d;
        cursor: not-allowed;
        transform: none;
        box-shadow: none;
      }
    }

    .cancel-btn {
      background: #f8f9fa;
      color: #6c757d;
      border: 2px solid #dee2e6;

      &:hover {
        background: #e9ecef;
        color: #495057;
        transform: translateY(-1px);
      }
    }
  }
}
